html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-size: 1.125rem;
  font-weight: 400;
  font-family: var(--font-body);
  background-color: var(--main-bg);
  background-image: url("/images/bg-mobile-light.jpg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: 100% auto;
}
@media (min-width: 40em) {
  body {
    padding-block: 5rem;
    background-image: url("/images/bg-desktop-light.jpg");
  }
}

body.dark-bg {
  background-image: url("/images/bg-mobile-dark.jpg");
}
@media (min-width: 40em) {
  body.dark-bg {
    background-image: url("/images/bg-desktop-dark.jpg");
  }
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --br-blue: hsl(220, 98%, 61%);
  --ck-bg-from: hsl(192, 100%, 67%);
  --ck-bg-to: hsl(280, 87%, 65%);
  --ul-bg: hsl(0, 0%, 98%);
  --main-bg: hsl(236, 33%, 92%);
  --linethrough-txt: hsl(233, 11%, 84%);
  --btn-txt: hsl(236, 9%, 61%);
  --ul-text: hsl(235, 19%, 35%);
  --btn-txt-hover: hsl(236, 33%, 92%);
  --vy-dk-g-blue2: hsl(237, 14%, 26%);
}

body.dark-theme {
  --main-bg: hsl(235, 21%, 11%);
  --ul-bg: hsl(235, 24%, 19%);
  --ul-text: hsl(234, 39%, 85%);
  --btn-txt: hsl(234, 11%, 52%);
  --linethrough-txt: hsl(233, 14%, 35%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --main-bg: hsl(235, 21%, 11%);
    --ul-bg: hsl(235, 24%, 19%);
    --ul-text: hsl(234, 39%, 85%);
    --btn-txt: hsl(234, 11%, 52%);
    --linethrough-txt: hsl(233, 14%, 35%);
  }
}
:root {
  --font-body: "Josefin Sans", sans-serif;
}

.container {
  max-width: 33.9375rem;
  width: 100%;
  margin-inline: auto;
  padding-inline: 1.5625rem;
}
@media (min-width: 40em) {
  .container {
    padding: 0;
  }
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2.5rem;
  padding-top: 3.125rem;
}
nav .logo {
  font-size: 1.375rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.625rem;
  color: var(--white);
}
nav button {
  position: relative;
  width: 1.375rem;
  height: 1.375rem;
  border: none;
  padding: 0;
  font-size: 0.625rem;
  font-family: var(--font-body);
  color: var(--white);
  background-color: transparent;
  background-image: url("/images/icon-moon.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  cursor: pointer;
}
nav button::before {
  content: "Night";
  position: absolute;
  top: 50%;
  right: 120%;
  transform: translateY(-50%);
}
nav button.night {
  background-image: url("/images/icon-sun.svg");
}
nav button.night::before {
  content: "Day";
}

.input {
  display: flex;
  gap: 0.75rem;
  border-radius: 0.3125rem;
  padding-block: 0.9375rem;
  padding-inline: 1.25rem;
  background-color: var(--ul-bg);
}
.input__checkbox {
  position: relative;
  display: block;
  width: 1.25rem;
  height: 1.25rem;
}
.input__checkbox--button {
  width: 1.25rem;
  height: 1.25rem;
  margin: 0;
  appearance: none;
  border: 0.0625rem solid var(--linethrough-txt);
  border-radius: 50%;
  cursor: pointer;
}
.input__checkbox > input[type=checkbox]:checked + span {
  content: url("/images/icon-check.svg");
  position: absolute;
  top: 0%;
  display: block;
  width: 1.25rem;
  height: 1.25rem;
  padding: 0.3125rem;
  border: none;
  border-radius: 50%;
  background-image: linear-gradient(145deg, var(--ck-bg-from), var(--ck-bg-to));
  cursor: pointer;
}
.input__text {
  width: 100%;
  outline: none;
  border: none;
  font-size: 0.75rem;
  font-family: var(--font-body);
  font-weight: 700;
  color: var(--ul-text);
  background-color: var(--ul-bg);
}

.filter {
  display: flex;
  justify-content: center;
  gap: 0.3125rem;
  margin-bottom: 2.6875rem;
  border-radius: 0.3125rem;
  padding-block: 1rem;
  background-color: var(--ul-bg);
}
.filter > * {
  border: none;
  font-size: 0.9375rem;
  font-weight: 700;
  font-family: var(--font-body);
  background-color: var(--ul-bg);
  color: var(--btn-txt);
  cursor: pointer;
}
.filter__active {
  color: var(--br-blue);
}

.note {
  margin-bottom: 3.125rem;
  text-align: center;
  font-size: 0.875rem;
  color: var(--btn-txt);
}